import { createRouter, createWebHistory } from "vue-router";
import { isAuth, setRouteName } from './utils/helpers'
const routes = [{
    path: '/',
    name: 'layouts',
    component: () => { return import('./layouts/DefaultLayout.vue') },
    children: [{
        path: '/',
        component: () => { return import('./components/pages/Home.vue') },
        name: 'home'
    },
    {
        path: '/search',
        component: () => { return import('./components/pages/Search.vue') },
        name: 'search'
    },
    {
        path: '/tracking/buy',
        component: () => { return import('./components/pages/TrackingBuy.vue') },
        name: 'tracking.buy'
    },
    {
        path: '/programs/:id/select/time',
        component: () => { return import('./components/pages/SelectTime.vue') },
        name: 'select_time'
    },
    {
        path: '/programs/:programId/sans/:sanId/select-chairs',
        component: () => { return import('./components/chairs/SelectChairs.vue') },
        name: 'seats'
    },
    {
        path: '/programs/:programId/sans/:sanId/select/info',
        component: () => { return import('./components/pages/SelectInfo.vue') },
        name: 'selectInfo'
    },
    {
        path: '/programs/:program_id/sans/:san_id/orders/:order_id/tickets',
        component: () => { return import('./components/pages/ShowTicket.vue') },
        name: 'Site.show.ticket',
        beforeEnter: (to, from, next) => {
            if (isAuth()) {
                next()
            }
            next({ name: 'auth' })
        }
    },
    {
        path: '/payment/factor/:number',
        component: () => { return import('./components/pages/PaymentFactor.vue') },
        name: 'payment.factor'
    },
    {
        path: '/authenticate',
        component: () => { return import('./components/Auth.vue') },
        name: 'auth'
    },
    {
        path: '/:pathMatch(.*)*',
        component: () => { return import('./components/NotFound.vue') },
        name: 'notfound'
    }
    ]
},
{
    path: '/panel',
    component: () => { return import('./layouts/UserPanelLayout') },
    name: 'panel_index',
    redirect: { name: 'panel.overview' },
    meta: {
        authRequired: true
    },
    children: [{
        path: '/overview',
        component: () => { return import('./components/users/OverViewUser.vue') },
        name: 'panel.overview',
        meta: {
            authRequired: true
        },
    }, {
        path: 'programs/:program_id/sans/:san_id/orders/:order_id/show/tickets',
        component: () => { return import('./components/users/Ticket.vue') },
        name: 'show.ticket',
        meta: {
            authRequired: true
        },
    },
    {
        path: 'orders',
        component: () => { return import('./components/users/Orders.vue') },
        name: 'panel.orders',
        meta: {
            authRequired: true
        },
    },
    {
        path: 'wallets',
        component: () => { return import('./components/users/Wallet.vue') },
        name: 'panel.wallets',
        meta: {
            authRequired: true
        },
    },
    {
        path: 'profile',
        component: () => { return import('./components/users/Profile.vue') },
        name: 'panel.profile',
        meta: {
            authRequired: true
        },
    }
    ],
    beforeEnter: (to, from, next) => {
        if (isAuth()) {
            next()
        }
        next({ name: 'auth' })
    }
},
];

const router = createRouter({
    history: createWebHistory(),
    routes
});

router.beforeEach((to, from, next) => {
    next()
    setRouteName(to.name)
    // let isAuth = false
    // if (to.meta.isAuth && !isAuth) {
    //     next({ name: 'home' })
    // } else {}
})

export default router;