import { defineStore } from "pinia";

export const useCurrentRouteNameStore = defineStore('current_route_name', {
    state: () => {
        return {
            name: '',
        }
    },
    getters: {
        getName(state) {
            return state.name
        },
    },
    actions: {
        setName(name) {
            this.name = name
            console.log('state = ' + this.name)
        }
    }
})