import api from '@/axios/api';
import { useCartStore } from '../store/cart'
export const isAuth = () => {
    const token = localStorage.getItem('token') || '';
    return token
}
import { useCurrentRouteNameStore } from '../store/current-route'
export const setRouteName = (name) => {
    const currentRouteStore = useCurrentRouteNameStore()
    currentRouteStore.setName(name)
}

export const handleError = (err) => {
    let errors = err.response.data.message;

    let messages = [];

    Object.keys(errors).map(err => {
        messages.push(errors[err]);
    })

    return messages;
}

export function numberFormat(number) {
    return Intl.NumberFormat().format(number);
}

export function nowDate(lang = 'en') {
    return new Date().toLocaleDateString()
}

export async function checkIsValidTicket(apiUrl) {
    const cartStore = useCartStore()
    let res = {
        status: 'success',
        message: "",
        expiredAtTickets: []
    }
    try {
        let data = {
            seats: cartStore.getCart.map(item => item.id)
        }
        await api.post(apiUrl, data);

    } catch (err) {
        if (err.response.status == 422 && err.response.data.status === 'error') {
            toast.error(handleError(err).join(''), {
                timeout: 2000
            });
            res.status = 'error'
            if (err.response.data.data.expiredAtTickets) {
                res.expiredAtTickets = err.response.data.data.expiredAtTickets;
            }
        }
    } finally {
        return res;
    }
}