import { defineStore } from "pinia";

export const useCartStore = defineStore('cart', {
    state: () => {
        return {
            cart: JSON.parse(localStorage.getItem('cart')) || [],
            discount: JSON.parse(localStorage.getItem('discount')) || 0
        }
    },
    getters: {
        getCart(state) {
            return state.cart
        },
        getTotal(state) {
            return state.cart.reduce((sum, item) => sum += item.price, 0)
        },
        getCount(state) {
            return state.cart.length
        },
        getTax() {
            if (this.getTotal > 0) {
                return 9 * (this.getTotal / 100)
            }
            return 0;
        },
        getDiscount(state) {
            return state.discount
        }
    },
    actions: {
        addSeatToCart(seat) {
            this.cart.push(seat)
            localStorage.setItem('cart', JSON.stringify(this.getCart));
        },
        clearCart() {
            this.cart = [];
            this.discount = 0;
            localStorage.removeItem('cart');
            localStorage.removeItem('discount');
        },
        setDiscount(amount) {
            this.discount = amount
            localStorage.setItem('discount', amount);
        },
        removeSeatFromCart(seatId) {
            this.cart = this.cart.filter(item => item.id != seatId)
            localStorage.setItem('cart', JSON.stringify(this.getCart));
        },
        hasCart(seatId) {
            return this.getCart.map(item => item.id).includes(seatId)
        },
    }
})